.about{padding:4rem 0;background:transparent;color:var(--muted)}
.about .container{max-width:900px;margin:0 auto;padding:0 1rem}
.about h2{color:#fff;margin-bottom:1rem}
.about p{line-height:1.6;color:var(--muted)}

.about-grid{display:grid;grid-template-columns:1fr 1fr;gap:1.25rem;margin-top:1.5rem}
.about-grid .bio{background:var(--card);padding:1rem;border-radius:8px;border:1px solid rgba(155,89,255,0.04)}
.about-grid .skills{background:transparent;padding:1rem;border-radius:8px}

/* Skills styling */
.about-grid .skills h3 {
  color: #fff;
  margin-bottom: 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background: linear-gradient(135deg, #fff, var(--accent));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.skill-category {
  margin-bottom: 2rem;
}

.skill-category h4 {
  color: var(--accent);
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(155, 89, 255, 0.2);
}

.skill-item {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  width: 100%;
}

.skill-item:hover {
  transform: translateX(4px);
}

.skill-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.skill-name {
  color: #fff;
  font-weight: 500;
  font-size: 0.95rem;
  transition: all 0.3s ease;
}

.skill-item:hover .skill-name {
  color: var(--accent);
}

.skill-percent {
  color: var(--accent);
  font-weight: 700;
  font-size: 0.95rem;
  min-width: 45px;
  text-align: right;
  transition: all 0.3s ease;
}

.skill-item:hover .skill-percent {
  transform: scale(1.1);
  text-shadow: 0 0 10px rgba(155, 89, 255, 0.5);
}

/* Progress Bar with gradient */
.skill-bar {
  height: 8px;
  background: linear-gradient(
    90deg,
    rgba(155, 89, 255, 0.2),
    rgba(100, 50, 255, 0.1)
  );
  border-radius: 4px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 0 10px rgba(155, 89, 255, 0.1) inset;
  width: 100%;
  max-width: 100%;
}

.skill-bar::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: var(--skill-width, 0%);
  background: linear-gradient(
    90deg,
    var(--accent),
    var(--accent-2),
    rgba(155, 89, 255, 0.6)
  );
  background-size: 200% 100%;
  animation: gradientShift 2s ease-in-out infinite;
  border-radius: 4px;
  box-shadow: 0 0 15px rgba(155, 89, 255, 0.6),
              0 0 30px rgba(155, 89, 255, 0.3);
  transition: width 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

@keyframes gradientShift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.skill-item:hover .skill-bar::before {
  box-shadow: 0 0 20px rgba(155, 89, 255, 0.8),
              0 0 40px rgba(155, 89, 255, 0.4);
}

.about-grid .bio ul{margin-top:.6rem;padding-left:1rem;color:var(--muted)}

@media (max-width:820px){
	.about-grid{grid-template-columns:1fr;}
}
