.team {
  padding: 4rem 0;
  background: transparent;
}

.team .container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 1rem;
}

.team h2 {
  color: #fff;
  font-size: 2.2rem;
  margin-bottom: 0.5rem;
  text-align: center;
}

.team-subtitle {
  color: var(--muted);
  text-align: center;
  margin-bottom: 3rem;
  font-size: 1rem;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

/* Team Card */
.team-card {
  background: var(--card);
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid rgba(155, 89, 255, 0.1);
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  display: flex;
  flex-direction: column;
  height: 100%;
}

.team-card:hover {
  border-color: rgba(155, 89, 255, 0.3);
  box-shadow: 0 10px 30px rgba(155, 89, 255, 0.15);
  transform: translateY(-8px);
}

/* Team Member Photo */
.team-card-image {
  position: relative;
  width: 100%;
  height: 250px;
  overflow: hidden;
  background: linear-gradient(135deg, rgba(155, 89, 255, 0.2), rgba(100, 50, 255, 0.1));
}

.team-member-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s ease;
  filter: brightness(0.95) contrast(1.05);
}

.team-card:hover .team-member-photo {
  transform: scale(1.1);
  filter: brightness(1.05) contrast(1.15);
}

/* Team Card Content */
.team-card-content {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.team-card-content h3 {
  color: #fff;
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
  font-weight: 600;
}

.team-role {
  color: var(--accent);
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 0.75rem;
}

.team-bio {
  color: var(--muted);
  font-size: 0.9rem;
  line-height: 1.5;
  margin-bottom: auto;
  margin-bottom: 1rem;
}

/* Team Links */
.team-links {
  display: flex;
  gap: 1rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(155, 89, 255, 0.1);
}

.team-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background: rgba(155, 89, 255, 0.1);
  color: var(--accent);
  text-decoration: none;
  transition: all 0.3s ease;
  border: 1px solid rgba(155, 89, 255, 0.2);
  font-size: 1.1rem;
}

.team-link:hover {
  background: var(--accent);
  color: #000;
  transform: scale(1.1);
  box-shadow: 0 0 15px rgba(155, 89, 255, 0.5);
}

/* Responsive */
@media (max-width: 820px) {
  .team h2 {
    font-size: 1.8rem;
  }

  .team-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .team-card-image {
    height: 200px;
  }
}

@media (max-width: 480px) {
  .team {
    padding: 2rem 0;
  }

  .team h2 {
    font-size: 1.5rem;
  }

  .team-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .team-card-content {
    padding: 1rem;
  }

  .team-card-image {
    height: 180px;
  }
}
