.testimonials{
    padding:6rem 0;
    position:relative;
    overflow:hidden;
    background:linear-gradient(
        165deg,
        rgba(155,89,255,0.05) 0%,
        transparent 50%,
        rgba(106,13,173,0.03) 100%
    );
    min-height:300px;
    display:flex;
    align-items:center
}

.container {
    width: 100%;
    overflow: hidden;
}

.testi-marquee {
    position: relative;
    width: 100%;
    overflow: hidden;
}

.testi-track {
    display: flex;
    gap: 2rem;
    animation: scroll 40s linear infinite;
    padding: 1rem 0;
}

.testi-track:hover {
    animation-play-state: paused;
}

.testi-item {
    flex: 0 0 400px;
    background: rgba(155,89,255,0.08);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(155,89,255,0.1);
    border-radius: 16px;
    padding: 2rem;
    transition: all 0.4s ease;
    transform: scale(0.98);
}

.testi-item:hover {
    transform: scale(1) translateY(-5px);
    background: rgba(155,89,255,0.12);
    border-color: rgba(155,89,255,0.3);
    box-shadow: 
        0 20px 40px -12px rgba(155,89,255,0.25),
        0 0 20px -4px rgba(155,89,255,0.1);
}

.testi-text {
    color: #fff;
    font-size: 1.125rem;
    line-height: 1.6;
    margin: 0 0 1rem;
    position: relative;
}

.testi-who {
    color: var(--accent);
    font-size: 0.9375rem;
    font-weight: 500;
}

@keyframes scroll {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(calc(-100% - 2rem));
    }
}

/* Header styling */
.section-header{
    text-align:center;
    margin-bottom:4rem
}

.header-badge{
    display:inline-flex;
    align-items:center;
    gap:0.5rem;
    background:rgba(155,89,255,0.1);
    color:var(--accent);
    padding:0.5rem 1rem;
    border-radius:100px;
    font-weight:500;
    font-size:0.9375rem;
    margin-bottom:1rem;
    border:1px solid rgba(155,89,255,0.1)
}

.header-badge svg{
    width:16px;
    height:16px;
    animation:pulse 2s infinite
}

@keyframes shimmer {
    0% {
        transform:translateX(-100%) skewX(-20deg);
        opacity:0;
    }
    20% {
        opacity:1;
    }
    40%, 100% {
        transform:translateX(100%) skewX(-20deg);
        opacity:0;
    }
}

@keyframes slideUpScale {
    0% {
        opacity:0;
        transform:translateY(20px) scale(0.95);
    }
    100% {
        opacity:1; 
        transform:translateY(0) scale(1);
    }
}

.section-header h2{
    font-size:clamp(2rem, 4vw, 2.75rem);
    background:linear-gradient(90deg, #fff 30%, var(--accent-2));
    background-clip:text;
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;
    margin:0 0 1rem
}

.section-desc{
    color:var(--muted);
    font-size:1.125rem;
    max-width:600px;
    margin:0 auto;
    line-height:1.7
}

/* Testimonial cards grid */
.testi-grid{
    display:grid;
    grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));
    gap:2rem;
    perspective:1000px
}

.testi-card{
    background:linear-gradient(165deg,
        rgba(155,89,255,0.08) 0%,
        rgba(106,13,173,0.05) 100%
    );
    border-radius:16px;
    padding:1.75rem;
    border:1px solid rgba(155,89,255,0.1);
    transition:all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
    opacity:0;
    transform:translateY(20px) scale(0.95);
    animation:slideUpScale 0.6s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
    position:relative;
    overflow:hidden;
    backdrop-filter:blur(10px)
}

.testi-card::before{
    content:'';
    position:absolute;
    inset:0;
    background:linear-gradient(120deg,
        transparent,
        rgba(155,89,255,0.1),
        transparent
    );
    opacity:0;
    transition:opacity 0.4s ease;
    transform:translateX(-100%) skewX(-20deg);
    animation:shimmer 3s infinite
}

.testi-card:nth-child(2){
    animation-delay:0.2s
}

.testi-card:nth-child(3){
    animation-delay:0.4s
}

.testi-card:hover{
    transform:translateY(-8px);
    border-color:rgba(155,89,255,0.2);
    box-shadow:
        0 20px 40px -12px rgba(155,89,255,0.3),
        0 0 20px -4px rgba(155,89,255,0.1)
}

/* Card header with avatar */
.testi-header{
    display:grid;
    grid-template-columns:auto 1fr auto;
    gap:1rem;
    align-items:center;
    margin-bottom:1.25rem
}

.testi-avatar{
    position:relative;
    width:56px;
    height:56px
}

.testi-avatar img{
    width:100%;
    height:100%;
    object-fit:cover;
    border-radius:12px
}

.avatar-badge{
    position:absolute;
    bottom:-6px;
    right:-6px;
    background:linear-gradient(135deg,
        rgba(155,89,255,0.9),
        rgba(106,13,173,0.9)
    );
    width:24px;
    height:24px;
    border-radius:6px;
    display:flex;
    align-items:center;
    justify-content:center;
    color:#fff;
    font-size:12px;
    border:2px solid var(--background)
}

.testi-meta h4{
    color:#fff;
    margin:0;
    font-size:1.1rem;
    font-weight:600;
    background:linear-gradient(90deg,#fff,var(--accent-2));
    background-clip:text;
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent
}

.testi-meta p{
    color:var(--muted);
    margin:0.25rem 0 0;
    font-size:0.9375rem
}

.testi-rating{
    display:flex;
    gap:0.25rem;
    color:var(--accent)
}

.star-filled{
    fill:currentColor
}

/* Testimonial content */
.testi-content p{
    color:var(--muted);
    line-height:1.7;
    font-size:1rem;
    margin:0
}

/* Stats section */
.testi-stats{
    display:flex;
    justify-content:center;
    gap:4rem;
    margin-top:4rem;
    padding-top:4rem;
    border-top:1px solid rgba(155,89,255,0.1)
}

.stat-item{
    text-align:center;
    transition:all 0.3s ease-out
}

.stat-item:hover{
    transform:translateY(-4px)
}

.stat-number{
    display:block;
    font-size:2.5rem;
    font-weight:700;
    background:linear-gradient(90deg, #fff, var(--accent-2));
    background-clip:text;
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;
    margin-bottom:0.5rem
}

.stat-label{
    color:var(--muted);
    font-size:1rem
}

/* Background elements */
.testi-bg{
    position:absolute;
    inset:0;
    overflow:hidden;
    pointer-events:none
}

.bg-circle{
    position:absolute;
    border-radius:50%;
    background:rgba(155,89,255,0.03);
    filter:blur(40px)
}

.circle-1{
    width:400px;
    height:400px;
    top:-100px;
    left:-100px
}

.circle-2{
    width:500px;
    height:500px;
    bottom:-200px;
    right:-100px;
    background:rgba(106,13,173,0.03)
}

.bg-grid{
    position:absolute;
    inset:0;
    background-image:
        linear-gradient(rgba(155,89,255,0.03) 1px, transparent 1px),
        linear-gradient(90deg, rgba(155,89,255,0.03) 1px, transparent 1px);
    background-size:40px 40px;
    mask-image:radial-gradient(circle at 50% 50%, black, transparent 70%)
}

/* Animations */
@keyframes slideUp{
    to{
        opacity:1;
        transform:translateY(0)
    }
}

@keyframes pulse{
    0%, 100%{
        transform:scale(1)
    }
    50%{
        transform:scale(1.1)
    }
}

/* Responsive design */
@media (max-width:768px) {
    .testimonials{
        padding:4rem 0
    }

    .testi-grid{
        grid-template-columns:1fr;
        gap:1.5rem
    }

    .testi-stats{
        flex-direction:column;
        gap:2rem;
        text-align:center
    }

    .stat-number{
        font-size:2rem
    }
}

@media (min-width:769px) and (max-width:1024px) {
    .testi-grid{
        grid-template-columns:repeat(2, 1fr)
    }
}
