.profile-photo-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 2rem 0;
  padding-top: 1rem;
}

.profile-photo-wrapper {
  position: relative;
  width: 280px;
  height: 280px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Profile Photo Image */
.profile-photo {
  width: 280px;
  height: 280px;
  border-radius: 20px;
  object-fit: cover;
  object-position: center top;
  position: relative;
  z-index: 2;
  
  /* Tone down brightness - more natural look */
  filter: brightness(0.95) contrast(1.05);
  
  /* Smooth shadow */
  box-shadow: 0 20px 40px rgba(155, 89, 255, 0.15),
              0 0 60px rgba(155, 89, 255, 0.08),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
  
  /* Transition untuk effect hover */
  transition: transform 0.4s cubic-bezier(0.2, 0.9, 0.2, 1),
              filter 0.4s cubic-bezier(0.2, 0.9, 0.2, 1),
              box-shadow 0.4s cubic-bezier(0.2, 0.9, 0.2, 1);
}

/* Gradient Border Animation */
.profile-photo-gradient-border {
  position: absolute;
  inset: -3px;
  border-radius: 20px;
  padding: 3px;
  background: linear-gradient(
    45deg,
    var(--accent),
    var(--accent-2),
    var(--accent),
    var(--accent-2)
  );
  background-size: 300% 300%;
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask-composite: exclude;
  z-index: 1;
  opacity: 0;
  animation: gradientBorder 6s ease infinite;
  transition: opacity 0.4s cubic-bezier(0.2, 0.9, 0.2, 1);
}

/* Shine effect - permanent as background */
.profile-photo-shine {
  position: absolute;
  inset: 0;
  border-radius: 20px;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.15) 0%,
    rgba(255, 255, 255, 0) 50%,
    rgba(0, 0, 0, 0.05) 100%
  );
  pointer-events: none;
  z-index: 3;
  opacity: 1;
  transition: opacity 0.4s cubic-bezier(0.2, 0.9, 0.2, 1);
}

/* Hover Effect */
.profile-photo-wrapper:hover .profile-photo {
  /* Scale up - no vertical movement */
  transform: scale(1.05);
  
  /* Brightness increase */
  filter: brightness(1.1) saturate(1.1);
  
  /* Enhanced shadow on hover */
  box-shadow: 0 30px 60px rgba(155, 89, 255, 0.25),
              0 0 80px rgba(155, 89, 255, 0.15),
              inset 0 1px 0 rgba(255, 255, 255, 0.15);
}

.profile-photo-wrapper:hover .profile-photo-gradient-border {
  opacity: 1;
}

.profile-photo-wrapper:hover .profile-photo-shine {
  opacity: 1;
}

/* Gradient Border Animation */
@keyframes gradientBorder {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Responsive */
@media (max-width: 768px) {
  .profile-photo-wrapper {
    width: 240px;
    height: 240px;
  }
  
  .profile-photo {
    width: 240px;
    height: 240px;
  }
  
  .profile-photo-gradient-border {
    inset: -2px;
    padding: 2px;
  }
  
  .profile-photo-container {
    margin: 1.5rem 0;
  }
}

@media (max-width: 480px) {
  .profile-photo-wrapper {
    width: 200px;
    height: 200px;
  }
  
  .profile-photo {
    width: 200px;
    height: 200px;
  }
  
  .profile-photo-container {
    margin: 1rem 0;
  }
}
